import PropTypes from 'prop-types';
import React, { Component } from 'react';

const propTypes = {
  x: PropTypes.number.isRequired,
  y: PropTypes.number.isRequired,
  keyProp: PropTypes.string.isRequired,
  labelProp: PropTypes.string.isRequired,
  nodeKey: PropTypes.string.isRequired
};

export default class Node extends Component {
  plusSignHandler = event => {
    event.stopPropagation(); // empche la selection du noeud qui porte le signe plus
    this.props.addNewChildNode(this.props.nodeKey);
    return event;
  };

  mainNodeLeftPlusSignHandler = event => {
    // spcialement pour le noeud principal, signe plus  gauche
    event.stopPropagation(); // empche la selection du noeud qui porte le signe plus
    this.props.addNewChildNode(this.props.nodeKey, 'left');
    return event;
  };

  onDragStart = event => {
    event.stopPropagation(); // empche le paning
    this.props.onDragStart(this.props.name, this.props.nodeKey); // prvient le mindmap de quel noeud est dragg
    return event;
  };

  onDragStopRight = event => {
    event.stopPropagation(); // empche le paning
    this.props.onDragStop(this.props.nodeKey, 'right'); // informe le mindmap de l o le noeud est dropp
    return event;
  };

  onDragStopLeft = event => {
    event.stopPropagation(); // empche le paning
    this.props.onDragStop(this.props.nodeKey, 'left'); // informe le mindmap de l o le noeud est dropp
    return event;
  };

  onDragStopAbove = event => {
    event.stopPropagation(); // empche le paning
    this.props.onDragStop(this.props.nodeKey, 'above'); // informe le mindmap de l o le noeud est dropp
    return event;
  };

  onDragStopBelow = event => {
    event.stopPropagation(); // empche le paning
    this.props.onDragStop(this.props.nodeKey, 'below'); // informe le mindmap de l o le noeud est dropp
    return event;
  };

  nodeTextLength = () => {
    return this.props.name.length * 9;
  };

  handleLeftClick = () => {
    this.props.onNodeSelect(this.props.node.data);
  };

  handleRightClick = e => {
    this.props.onNodeSelect(this.props.node.data);
    this.props.openContextMenu(e);
  };

  createNode() {
    if (!this.props.parent) {
      // we are in main node
      return (
        <svg
          className="svg-rect"
          width={this.nodeTextLength() + 6}
          height="30"
          y="-10"
        >
          <rect
            className={
              this.props.currentNode.nodeKey === this.props[this.props.keyProp]
                ? 'selected-node'
                : 'main-node'
            }
            width={this.nodeTextLength() + 6}
            height={30}
          />
          <text
            x="50%"
            y="50%"
            textAnchor="middle"
            dy=".3em"
            textDecoration="underline"
            className="node"
          >
            /{this.props.name}
          </text>
        </svg>
      );
    }
    const label =
      (this.props.type === 'reference' && this.props.isArray ? '[' : '') +
      (this.props.type === 'reference' ? '#' : '') +
      (this.props.isSubPath === true ? '/' : '') +
      (this.props.type === 'array' ? '[' : '') +
      (this.props.type === 'operation' ? '_' : '') +
      this.props.name +
      (this.props.type === 'array' ? ']' : '') +
      (this.props.type === 'reference' && this.props.isArray ? ']' : '');
    return (
      <g>
        <rect
          className={
            this.nodeTextLength() === 0 ? 'error-node' : 'noerror-node'
          }
          width={25}
          height={20}
          y="-17"
          x={this.props.direction === 'left' ? -25 : 0}
        />
        <rect
          className={
            this.props.currentNode.nodeKey === this.props[this.props.keyProp]
              ? 'selected-node'
              : 'unselected-node'
          }
          width={this.nodeTextLength()}
          height={20}
          y="-17"
          x={this.props.direction === 'left' ? -this.nodeTextLength() : 0}
        />
        <text
          textAnchor={this.props.direction === 'right' ? 'start' : 'end'}
          {...this.props.textProps}
          className="node"
          fontStyle={this.props.type === 'operation' ? 'italic' : 'normal'}
          fill={this.props.type === 'operation' ? 'CornflowerBlue' : 'black'}
          textDecoration={this.props.isSubPath === true ? 'underline' : ''}
          fontWeight={this.props.abstract === true ? 'bold' : ''}
        >
          {label}
        </text>
        <circle
          r="10"
          className={this.props.isDraggingMoving ? 'active' : 'inactive'}
          cy="-13"
          cx={
            this.props.direction === 'left'
              ? -this.nodeTextLength() / 2
              : this.nodeTextLength() / 2
          }
          onMouseUp={this.onDragStopAbove}
        />
        <circle
          r="10"
          className={this.props.isDraggingMoving ? 'active' : 'inactive'}
          cy="7"
          cx={
            this.props.direction === 'left'
              ? -this.nodeTextLength() / 2
              : this.nodeTextLength() / 2
          }
          onMouseUp={this.onDragStopBelow}
        />
      </g>
    );
  }

  render() {
    let leftPlusSign;
    if (this.props.parent === null) {
      leftPlusSign = (
        <g onClick={this.mainNodeLeftPlusSignHandler}>
          <rect className="plussign" width="11" height="11" y="0" x="-8" />
          <text className="plussign" y="11" x="-7">
            +
          </text>
        </g>
      );
    }

    return (
      <g
        {...this.props.gProps}
        transform={`translate(${this.props.y}, ${this.props.x - 5})`}
        onContextMenu={e => this.handleRightClick(e)}
        onMouseDown={this.props.parent !== null ? this.onDragStart : null}
      >
        <g className="allnodes" onClick={this.handleLeftClick}>
          {this.createNode()}
          <circle
            r="10"
            className={
              this.props.isDraggingMoving &&
              (this.props.direction === 'left' || this.props.parent === null)
                ? 'active'
                : 'inactive'
            }
            cy="-3"
            cx={
              -20 -
              (this.props.direction === 'left' ? this.nodeTextLength() : 0)
            }
            onMouseUp={this.onDragStopLeft}
          />
          <circle
            r="10"
            className={
              this.props.isDraggingMoving && this.props.direction === 'right'
                ? 'active'
                : 'inactive'
            }
            cy="-3"
            cx={
              (this.props.direction === 'right' ? this.nodeTextLength() : 0) +
              20
            }
            onMouseUp={this.onDragStopRight}
          />
          {leftPlusSign}
          {this.props.type !== 'reference' && (
            <g onClick={this.plusSignHandler}>
              <rect
                className={
                  this.props.isDraggingMoving ? 'plussign-dragging' : 'plussign'
                }
                width="11"
                height="11"
                y="0"
                x={
                  this.props.direction === 'right'
                    ? this.nodeTextLength()
                    : -8 - this.nodeTextLength()
                }
              />
              <text
                className={
                  this.props.isDraggingMoving ? 'plussign-dragging' : 'plussign'
                }
                y="11"
                x={
                  this.props.direction === 'right'
                    ? 1 + this.nodeTextLength()
                    : -7 - this.nodeTextLength()
                }
              >
                +
              </text>
            </g>
          )}
        </g>
      </g>
    );
  }
}

Node.propTypes = propTypes;
