/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
import React, { PureComponent } from 'react';

export default class ContextMenu extends PureComponent {
  render() {
    return (
      <ul className="custom-ul" id="container-menu">
        {(this.props.currentNode.type !== 'reference' && (
          <li data-action="addChild" onClick={this.props.insert}>
            Add Child
          </li>
        )) || (
          <li data-action="open" onClick={this.props.open}>
            Open
          </li>
        )}
        <li data-action="copy" onClick={this.props.copy}>
          Copy
        </li>
        <li data-action="cut" onClick={this.props.cut}>
          Cut
        </li>
        <li data-action="paste" onClick={this.props.paste}>
          Paste
        </li>
        <li data-action="delete" onClick={this.props.delete}>
          Delete
        </li>
      </ul>
    );
  }
}
