/* eslint-disable no-param-reassign */
/* eslint-disable no-shadow */
/* eslint-disable prefer-const */
/* eslint-disable class-methods-use-this */
/* eslint-disable react/forbid-prop-types */
/* eslint-disable react/default-props-match-prop-types */
/* eslint-disable react/require-default-props */
import clone from 'clone';
import { easeQuadOut } from 'd3-ease';
import { hierarchy, tree } from 'd3-hierarchy';
import PropTypes from 'prop-types';
import React, { Component } from 'react';
import uniqid from 'uniqid';
import _ from 'lodash';
import Animated from './Animated';
import ModelAPI from '../../api/ModelAPI';
import {
  searchTreeByKey,
  isAncestorOf,
  cloneTree,
  addNewChildNode,
} from '../../helpers/tree_helper';

const propTypes = {
  animated: PropTypes.bool.isRequired,
  children: PropTypes.node,
  duration: PropTypes.number.isRequired,
  easing: PropTypes.func.isRequired,
  steps: PropTypes.number.isRequired,
  keyProp: PropTypes.string.isRequired,
  labelProp: PropTypes.string.isRequired,
  getChildren: PropTypes.func.isRequired,
  margins: PropTypes.shape({
    bottom: PropTypes.number.isRequired,
    left: PropTypes.number.isRequired,
    right: PropTypes.number.isRequired,
    top: PropTypes.number.isRequired,
  }).isRequired,
  gProps: PropTypes.object.isRequired,
  pathProps: PropTypes.object.isRequired,
  textProps: PropTypes.object.isRequired,
};

const defaultProps = {
  animated: false,
  duration: 50,
  easing: easeQuadOut,
  getChildren: n => n.children,
  steps: 5,
  keyProp: 'nodeKey',
  labelProp: 'name',
  margins: {
    bottom: 10,
    left: 100,
    right: 150,
    top: 10,
  },
  nodeWidth: 100,
  gProps: {
    className: 'node',
  },
  pathProps: {
    className: 'link',
  },
  textProps: {},
};

const oneCharWidth = 10;

export default class Tree extends Component {
  previousStates = [];

  state = {
    toCopyNodeKey: null,
    toCutNodeKey: null,
    event: '',
    fullScreen: false,
    swaggerHtml: '',
  };

  keyHandler = ({ keydown }) => {
    if (keydown.event) {
      if (keydown.event.which === 113) {
        this.f2Key();
      }
      if (keydown.event.which === 40) {
        this.downKeyHandler();
      }
      if (keydown.event.which === 38) {
        this.upKeyHandler();
      }
      if (keydown.event.which === 37) {
        this.leftKeyHandler();
      }
      if (keydown.event.which === 39) {
        this.rightKeyHandler();
      }
      if (keydown.event.which === 66 && keydown.event.ctrlKey) {
        this.boldHandler();
      }
      if (keydown.event.which === 67 && keydown.event.ctrlKey) {
        this.copyHandler();
      }
      if (keydown.event.which === 88 && keydown.event.ctrlKey) {
        this.cutHandler();
      }
      if (keydown.event.which === 86 && keydown.event.ctrlKey) {
        this.pasteHandler();
      }
      if (
        (keydown.event.which === 90 && keydown.event.ctrlKey) ||
        (keydown.event.which === 90 && keydown.event.metaKey)
      ) {
        this.props.onUndo();
      }
      if (keydown.event.which === 13) {
        this.enterKeyHandler();
      }
      if (keydown.event.which === 45) {
        this.insertKeyHandler();
      }
      if (keydown.event.which === 46 || keydown.event.which === 8) {
        this.deleteKeyHandler();
      }
      if (
        keydown.event.which >= 65 &&
        keydown.event.which <= 90 &&
        !keydown.event.ctrlKey &&
        !keydown.event.metaKey
      ) {
        this.props.onLetterDown();
      }
    }
  };

  updateChildrenParentLinks = (children, newParentKey) => {
    if (children && children.length > 0) {
      children.forEach(child => {
        child.parent = newParentKey;
      });
    }
  };

  moveNode = (draggedNodeKey, destinationNodeKey, direction) => {
    if (
      destinationNodeKey &&
      draggedNodeKey &&
      draggedNodeKey !== destinationNodeKey &&
      !isAncestorOf(draggedNodeKey, destinationNodeKey, this.props.data)
    ) {
      const allData = cloneTree(this.props.data, null, false);
      const nodeWhereToAdd = searchTreeByKey(destinationNodeKey, allData);
      const nodeToDelete = searchTreeByKey(draggedNodeKey, allData);
      if (nodeToDelete.parent) {
        let parentNode = searchTreeByKey(nodeToDelete.parent, allData);
        parentNode.children.splice(
          parentNode.children.findIndex(v => v.nodeKey === draggedNodeKey),
          1
        );
        if (direction === 'left' || direction === 'right') {
          let newNode = cloneTree(nodeToDelete, nodeWhereToAdd.nodeKey);
          newNode.direction = direction;
          nodeWhereToAdd.children.push(newNode);
          this.updateChildrenParentLinks(newNode.children, newNode.nodeKey);
          this.props.onTreeUpdate({
            dataUpdated: allData,
            currentNode: newNode,
          });
        }
        if (direction === 'above' || direction === 'below') {
          if (nodeWhereToAdd.parent) {
            let parentNode = searchTreeByKey(nodeWhereToAdd.parent, allData);
            let destIndex = parentNode.children.findIndex(
              v => v.nodeKey === destinationNodeKey
            );
            let newNode = cloneTree(nodeToDelete, nodeWhereToAdd.parent);
            parentNode.children.splice(
              direction === 'above' ? destIndex : destIndex + 1,
              0,
              newNode
            );
            newNode.direction = nodeWhereToAdd.direction;
            this.updateChildrenParentLinks(newNode.children, newNode.nodeKey);
            this.props.onTreeUpdate({
              dataUpdated: allData,
              currentNode: newNode,
            });
          }
        }
      }
    }
  };

  getTreeDepthAndCardinality = (data, depth) => {
    this.cardinality += 1;
    if (this.maxDepth < depth) this.maxDepth = depth;
    if (data.children.length !== 0)
      data.children.forEach(n => this.getTreeDepthAndCardinality(n, depth + 1));
  };

  getTreeTextWidth = (data, textWidth, maxLength) => {
    if (data.children.length !== 0) {
      const lengths = data.children.map(n =>
        this.getTreeTextWidth(n, textWidth + n.name.length)
      );
      return Math.max(...lengths);
    }

    if (data.name && maxLength > textWidth + data.name.length) {
      return textWidth + data.name.length;
    }

    if (data.children.length === 0) {
      return 0;
    }

    return maxLength;
  };

  propagateDirection = (node, dir) => {
    node.direction = dir;
    node.children.forEach(c => {
      this.propagateDirection(c, dir);
    });
  };

  propagateShiftLeft = (node, horizontalShift) => {
    let nodeShift = node.data.name.length * oneCharWidth;
    if (node.depth === 0) nodeShift = 0;
    node.y += horizontalShift;
    if (node.children)
      node.children.forEach(c => {
        this.propagateShiftLeft(c, horizontalShift + nodeShift);
      });
  };

  propagateShiftRight = (node, horizontalShift) => {
    const nodeShift = node.data.name.length * oneCharWidth;
    if (node.children)
      node.children.forEach(c => {
        c.y += horizontalShift + nodeShift;
        this.propagateShiftRight(c, horizontalShift + nodeShift);
      });
  };

  nodePlacementAdaptation = (node, treeMaxLength) => {
    if (node.parent && node.parent.parent) {
      let maxLength = 0;
      node.parent.parent.children.forEach(uncle => {
        if (uncle.data.name.length > maxLength) {
          maxLength = uncle.data.name.length;
        }
      });
      treeMaxLength += maxLength;
      node.y +=
        (treeMaxLength -
          node.parent.data.name.length * (1 + 0.11 * (node.depth - 1))) *
        9;
    }

    if (node.children) {
      node.children = node.children.map(child => {
        return this.nodePlacementAdaptation(child, treeMaxLength);
      });
    }

    return node;
  };

  addNewChildNode = (nodeKey, direction) => {
    const { data, selectedNodeKey, node } = addNewChildNode(
      this.props.data,
      nodeKey,
      direction
    );
    if (data && selectedNodeKey && node) {
      this.props.onTreeUpdate({
        dataUpdated: data,
        currentNode: node,
      });
    }
  };

  openReferenceHandler = () => {
    ModelAPI.getModel(this.props.currentNode.referenceId).then(
      populatedModel => {
        if (populatedModel.freezeTag === '0') {
          this.props.history.push({
            pathname: '/tree',
            state: {
              modelId: this.props.currentNode.referenceId,
            },
          });
        } else {
          this.props.history.push({
            pathname: '/tree-frozen',
            state: {
              model: populatedModel,
            },
          });
        }
      }
    );
  };

  insertKeyHandler = () => {
    if (this.props.currentNode.type !== 'reference') {
      this.addNewChildNode(this.props.currentNode.nodeKey, null);
    }
  };

  deleteKeyHandler = () => {
    if (this.props.currentNode.nodeKey) {
      const allData = cloneTree(this.props.data, null, false);
      let nodeToDelete = searchTreeByKey(
        this.props.currentNode.nodeKey,
        allData
      );
      if (nodeToDelete.parent) {
        let parentNode = searchTreeByKey(nodeToDelete.parent, allData);
        parentNode.children.splice(
          parentNode.children.findIndex(
            v => v.nodeKey === this.props.currentNode.nodeKey
          ),
          1
        );
        this.props.onTreeUpdate({
          dataUpdated: allData,
          currentNode: parentNode,
        });
      }
    }
  };

  copyHandler = () => {
    if (this.props.currentNode.nodeKey) {
      this.setState({
        toCopyNodeKey: this.props.currentNode.nodeKey,
        toCutNodeKey: null,
      });
    }
  };

  cutHandler = () => {
    if (this.props.currentNode.nodeKey) {
      this.setState({
        toCopyNodeKey: null,
        toCutNodeKey: this.props.currentNode.nodeKey,
      });
    }
  };

  pasteHandler = () => {
    let allData = cloneTree(this.props.data, null, false);
    if (this.props.currentNode.nodeKey) {
      let nodeWhereToAdd = searchTreeByKey(
        this.props.currentNode.nodeKey,
        allData
      );

      // Cut
      if (
        this.state.toCutNodeKey &&
        !isAncestorOf(
          this.state.toCutNodeKey,
          this.props.currentNode.nodeKey,
          this.props.data
        )
      ) {
        let nodeToDelete = searchTreeByKey(this.state.toCutNodeKey, allData);
        let newNode = cloneTree(nodeToDelete, nodeWhereToAdd.nodeKey);
        let parentNode = searchTreeByKey(nodeToDelete.parent, allData);
        parentNode.children.splice(
          parentNode.children.findIndex(
            v => v.nodeKey === this.state.toCutNodeKey
          ),
          1
        );
        nodeWhereToAdd.children.push(newNode);
        this.props.onTreeUpdate({
          dataUpdated: allData,
          currentNode: newNode,
        });
      }

      // Copy
      if (this.state.toCopyNodeKey) {
        let nodeToCopy = searchTreeByKey(this.state.toCopyNodeKey, allData);
        if (nodeToCopy.parent) {
          let newNode = cloneTree(nodeToCopy, nodeWhereToAdd.nodeKey);
          nodeWhereToAdd.children.push(newNode);
          this.props.onTreeUpdate({
            dataUpdated: allData,
            currentNode: newNode,
          });
        }
      }
    }
  };

  sanitizeNode(node) {
    Object.keys(node).forEach(key => {
      if (node[key] && _.isString(node[key]) && node[key].indexOf('"') > -1) {
        node[key] = node[key].replace('"', '');
      }
    });
  }

  boldHandler() {
    this.props.onToolsMenuChange({
      abstract: !this.props.currentNode.abstract,
    });
  }

  enterKeyHandler() {
    if (this.props.currentNode.nodeKey) {
      const allData = cloneTree(this.props.data, null, false);
      let nodeWhereToAdd = searchTreeByKey(
        this.props.currentNode.nodeKey,
        allData
      );
      if (nodeWhereToAdd.parent) {
        const parentNode = searchTreeByKey(nodeWhereToAdd.parent, allData);
        const indexWhereToAdd = parentNode.children.indexOf(nodeWhereToAdd);
        const newNodeKey = uniqid();
        const newNode = {
          name: '',
          parent: parentNode.nodeKey,
          children: [],
          nodeKey: newNodeKey,
          type: 'string',
          referenceId: '',
          isArray: false,
          example: '',
          abstract: false,
          description: '',
          queryParams: '',
          direction: nodeWhereToAdd.direction,
          queryable: false,
          paginable: false,
          isInPostResponse: false,
          patchable: false,
          notImplemented: false,
          isSubPath: false,
          calculated: false,
          branchColor: parentNode.branchColor,
        };
        parentNode.children.splice(indexWhereToAdd + 1, 0, newNode);
        this.props.onTreeUpdate({
          dataUpdated: allData,
          currentNode: newNode,
        });
      }
    }
  }

  upKeyHandler() {
    if (this.props.currentNode.nodeKey) {
      const allData = this.props.data;
      let selectedNode = searchTreeByKey(
        this.props.currentNode.nodeKey,
        allData
      );
      if (selectedNode.parent) {
        let parentNode = searchTreeByKey(selectedNode.parent, allData);
        for (
          let indexWhereToGo = parentNode.children.indexOf(selectedNode) - 1;
          indexWhereToGo >= 0;
          indexWhereToGo -= 1
        ) {
          if (
            parentNode.children[indexWhereToGo].direction ===
            selectedNode.direction
          ) {
            this.props.onNodeSelect(
              parentNode.children[indexWhereToGo],
              allData
            );
            break;
          }
        }
      }
    }
  }

  downKeyHandler() {
    if (this.props.currentNode.nodeKey) {
      const allData = this.props.data;
      let selectedNode = searchTreeByKey(
        this.props.currentNode.nodeKey,
        allData
      );
      if (selectedNode.parent) {
        let parentNode = searchTreeByKey(selectedNode.parent, allData);
        let indexWhereToGo;
        for (
          indexWhereToGo = parentNode.children.indexOf(selectedNode) + 1;
          indexWhereToGo < parentNode.children.length;
          indexWhereToGo += 1
        ) {
          if (
            parentNode.children[indexWhereToGo].direction ===
            selectedNode.direction
          ) {
            this.props.onNodeSelect(
              parentNode.children[indexWhereToGo],
              allData
            );
            break;
          }
        }
      }
    }
  }

  leftKeyHandler() {
    if (this.props.currentNode.nodeKey) {
      const allData = this.props.data;
      let selectedNode = searchTreeByKey(
        this.props.currentNode.nodeKey,
        allData
      );
      if (selectedNode.nodeKey === this.props.data.nodeKey) {
        let indexWhereToGo = selectedNode.children.findIndex(
          n => n.direction === 'left'
        );
        if (indexWhereToGo !== -1) {
          this.props.onNodeSelect(
            selectedNode.children[indexWhereToGo],
            allData
          );
        }
      } else if (selectedNode.direction === 'right') {
        this.goToParent(selectedNode, allData);
      } else {
        this.goToFirstChild(selectedNode, allData);
      }
    }
  }

  rightKeyHandler() {
    if (this.props.currentNode.nodeKey) {
      const allData = this.props.data;
      const selectedNode = searchTreeByKey(
        this.props.currentNode.nodeKey,
        allData
      );
      if (selectedNode.direction === 'left') {
        this.goToParent(selectedNode, allData);
      } else {
        this.goToFirstChild(selectedNode, allData);
      }
    }
  }

  goToParent(selectedNode, allData) {
    if (selectedNode.parent) {
      const parentNode = searchTreeByKey(selectedNode.parent, allData);
      this.props.onNodeSelect(parentNode, allData);
    }
  }

  goToFirstChild(selectedNode, allData) {
    if (selectedNode.children && selectedNode.children.length > 0) {
      const indexWhereToGo = selectedNode.children.findIndex(
        n => n.direction === selectedNode.direction
      );
      if (indexWhereToGo !== -1) {
        this.props.onNodeSelect(selectedNode.children[indexWhereToGo], allData);
      }
    }
  }

  render() {
    const { isFocus, onNodeSelect, children, data, margins } = this.props;

    // Calcule une taille de svg adapte  la richesse de l'arbre
    // Pour la largeur, trouver la branche o il y a le plus de texte cumul,  gauche puis  droite
    const maxLeftLength = this.getTreeTextWidth(
      {
        children: data.children.filter(ld => ld.direction === 'left'),
      },
      0,
      120
    );
    const maxRightLength = this.getTreeTextWidth(
      {
        children: data.children.filter(ld => ld.direction === 'right'),
      },
      0,
      120
    );
    const contentHeight =
      600 +
      (maxLeftLength + maxRightLength) * oneCharWidth -
      margins.left -
      margins.right;

    // Pour la hauteur, on fait le nombre de champs divis par le nombre de niveaux. Poura tre amlior
    this.maxDepth = 0;
    this.cardinality = 0;
    this.getTreeDepthAndCardinality(data, 0);
    const contentWidth =
      200 +
      (60 * (this.cardinality + 1)) / (this.maxDepth + 1) -
      margins.top -
      margins.bottom;

    // Cre deux sous arbres gauche et droite
    data.children.forEach(c => {
      this.propagateDirection(c, c.direction);
    });

    const leftData = clone(data);
    leftData.children = leftData.children.filter(ld => ld.direction === 'left');
    this.propagateDirection(leftData, 'left');

    let rootLeft = tree().size([contentWidth, contentHeight / 2])(
      hierarchy(leftData)
    );

    rootLeft = this.nodePlacementAdaptation(rootLeft, 0);
    const nodesLeft = rootLeft.descendants();
    const linksLeft = rootLeft.links();
    this.propagateShiftLeft(nodesLeft[0], 0);

    const rightData = clone(data);
    rightData.children = rightData.children.filter(
      rd => rd.direction === 'right'
    );
    this.propagateDirection(rightData, 'right', 0);

    let rootRight = tree().size([contentWidth, contentHeight / 2])(
      hierarchy(rightData)
    );

    rootRight = this.nodePlacementAdaptation(rootRight, 0);
    const nodesRight = rootRight.descendants();
    const linksRight = rootRight.links();
    this.propagateShiftRight(nodesRight[0], 0);

    // Retourne le sous-arbre gauche et le positionne en face du droit
    const dh = nodesRight[0].x - nodesLeft[0].x;
    nodesLeft.forEach(nl => {
      nl.x += dh;
      nl.y = -nl.y;
    });

    // Rattache au noeud racine droite les enfants du noeud racine de gauche
    linksLeft.forEach(ll => {
      if (ll.source.data.nodeKey === nodesLeft[0].data.nodeKey) {
        // eslint-disable-next-line no-param-reassign
        ll.source = nodesRight[0];
      }
      if (ll.target.data.nodeKey === nodesLeft[0].data.nodeKey) {
        ll.target = nodesRight[0];
      }
    });
    nodesLeft.splice(0, 1);

    // Joint les deux sous-arbres
    const nodes = nodesLeft.concat(nodesRight);
    const links = linksLeft.concat(linksRight);

    // Mesure l'arbre rsultat puis recentre
    let minX = nodesRight[0].x;
    let maxX = nodesRight[0].x;
    let minY = nodesRight[0].y;
    let maxY = nodesRight[0].y;
    nodes.forEach(n => {
      if (minX > n.x) minX = n.x;
      if (maxX < n.x) maxX = n.x;
      if (minY > n.y) minY = n.y;
      if (maxY < n.y) maxY = n.y;
    });
    const shift = (minX + maxX) / 2; // Hauteur mediane
    nodes.forEach(n => {
      n.x -= shift;
    });
    maxX -= shift;
    minX -= shift;

    minY -= 100; // Centrage pour les petits mindmaps, comme celui du dpart from scratch
    return (
      <Animated
        {...this.props}
        {...this.state}
        minY={minX}
        maxY={maxX}
        minX={minY}
        maxX={maxY}
        links={links}
        nodes={nodes}
        centerX={nodes[0].y}
        centerY={nodes[0].x}
        cardinality={this.cardinality}
        open={() => this.openReferenceHandler()}
        insert={() => this.insertKeyHandler()}
        copy={() => this.copyHandler()}
        cut={() => this.cutHandler()}
        paste={() => this.pasteHandler()}
        delete={() => this.deleteKeyHandler()}
        moveNode={this.moveNode}
        addNewChildNode={this.addNewChildNode}
        onSaveTree={this.saveTree}
        onKeyPress={this.keyHandler}
        onNodeSelect={onNodeSelect}
        isFocus={isFocus}
      >
        {children}
      </Animated>
    );
  }
}

Tree.propTypes = propTypes;
Tree.defaultProps = defaultProps;
