/* eslint-disable consistent-return */
import React, { Component } from 'react';
import { Notification } from 'element-react';
import { withRouter } from 'react-router-dom';
import Slide from '@material-ui/core/Slide';
import VisibilityIcon from '@material-ui/icons/Visibility';
import IconButton from '@material-ui/core/IconButton';
import { withStyles } from '@material-ui/core/styles';
import ResizePanel from 'react-resize-panel';
import { trackPromise } from 'react-promise-tracker';
import Tree from '../mindmap/Tree';
import { updateTree } from '../../helpers/tree_helper';
import EditNodeMenu from '../mindmap/EditNodeMenu';
import LivePreview from '../mindmap/LivePreview';
import SwaggerAPI from '../../api/SwaggerAPI';
import ModelAPI from '../../api/ModelAPI';
import styles from '../../styles/MindmapView.css';
import { UserContext } from '../../contexts/UserContext';

class MindmapView extends Component {
  static contextType = UserContext;

  state = {
    history: [],
    saved: true,
    canBeSaved: false,
    currentNode: { nodeKey: '' },
    data: { children: [], name: '' },
    refinerRules: [],
    livePreviewSwagger: null,
    isLivePreviewOpen: true,
    isEditMenuOpen: true,
    focus: 'tree',
    modelId: this.props.location.state
      ? this.props.location.state.modelId
      : null,
    organizationId: this.props.location.state
      ? this.props.location.state.organizationId
      : null
  };

  intervalId = null;

  componentDidMount() {
    const { location, history } = this.props;

    if (!location.state) {
      history.push('/dashboard');
    } else {
      const { data, modelId, isPublic } = location.state;

      if (!isPublic) {
        ModelAPI.getModel(modelId).then(model => {
          this.setState(
            {
              data: model.data,
              refinerRules: model.refinerRules || [],
              currentNode: { ...model.data },
              canBeSaved: true
            },
            () => {
              this.saveModel(false);
              this.requestLivePreview(this.state.data);
            }
          );
        });
      } else {
        this.setState(
          {
            data,
            currentNode: { ...data }
          },
          () => {
            this.saveModel(false);
            this.requestLivePreview(this.state.data);
          }
        );
      }

      this.intervalId = setInterval(() => {
        if (this.state.canBeSaved) {
          this.saveModel(false);
        }
      }, 10000);
    }
  }

  componentWillUnmount() {
    clearInterval(this.intervalId);
    if (this.state.canBeSaved) {
      this.saveModel(true);
    }
  }

  onNodeSelect = node => {
    this.setState({
      currentNode: { ...node }
    });
  };

  onTreeUpdate = ({ dataUpdated, currentNode }) => {
    const { history, data } = this.state;
    history.push(data);
    this.setState({ data: dataUpdated, currentNode, history, saved: false });
    this.requestLivePreview(dataUpdated);
  };

  setModelLock = isLock => {
    const { modelId } = this.state;
    ModelAPI.setModelLock(modelId, isLock).catch(err => {
      if (err.response.status === 403) {
        Notification.error({
          title: 'Error',
          message:
            'You can only modify your own models. Please ask your team leader if you need a modification on this model.'
        });
      } else {
        Notification.error({
          title: 'Error',
          message: 'Could not lock/unlock. Please log in'
        });
      }
    });
  };

  saveModel = isExitSave => {
    // eslint-disable-next-line
    const { saved, modelId, data } = this.state;
    const { isPublic } = this.props.location.state;

    if (isPublic) {
      return;
    }

    if (saved && !isExitSave) {
      // Already saved, only maintain the lock
      return this.setModelLock(true);
    }

    if (saved && isExitSave) {
      // Already saved, only remove the lock
      return this.setModelLock(false);
    }

    const isLocked = !isExitSave;

    ModelAPI.saveModel(modelId, { data }, isLocked)
      .then(() => {
        this.setState({ saved: true });
      })
      .catch(err => {
        if (err.response.status === 403) {
          Notification.error({
            title: 'Error',
            message:
              'You can only modify your own models. Please ask your team leader if you need a modification on this model.'
          });
        } else {
          Notification.error({
            title: 'Error',
            message: 'Could not save. Please log in'
          });
        }
      });
  };

  onToolsMenuChange = node => {
    let { currentNode, data } = this.state;
    const { history } = this.state;

    if (node.verbs) {
      if (currentNode.verbs.includes(node.verbs[0])) {
        const newNode = {
          verbs: currentNode.verbs.filter(verb => verb !== node.verbs[0])
        };
        // eslint-disable-next-line no-prototype-builtins
        if (node.hasOwnProperty('isSubPath')) {
          newNode.isSubPath = node.isSubPath;
        }
        currentNode = { ...currentNode, ...newNode };
      } else {
        const newNode = { verbs: currentNode.verbs.concat(node.verbs) };
        // eslint-disable-next-line no-prototype-builtins
        if (node.hasOwnProperty('isSubPath')) {
          newNode.isSubPath = node.isSubPath;
        }
        currentNode = { ...currentNode, ...newNode };
      }
    } else {
      currentNode = { ...currentNode, ...node };
    }
    data = updateTree(data, currentNode);
    history.push(data);
    this.setState({
      data,
      history,
      currentNode,
      saved: false
    });
    this.requestLivePreview(data);
  };

  onPreviewClick = () => {
    const { isLivePreviewOpen, data } = this.state;
    this.setState({
      isLivePreviewOpen: !isLivePreviewOpen
    });

    if (!isLivePreviewOpen) {
      this.requestLivePreview(data);
    }
  };

  onEditClick = () => {
    const { isEditMenuOpen } = this.state;
    this.setState({
      isEditMenuOpen: !isEditMenuOpen
    });
  };

  onPreviewClose = () => {
    this.setState({ isLivePreviewOpen: false });
  };

  onUndo = () => {
    const { history } = this.state;
    if (history.length === 0) {
      return;
    }

    const data = history.pop();
    this.setState({ currentNode: data, data, history });
  };

  setFocus = onFocus => {
    const { focus } = this.state;
    if (focus !== onFocus) {
      this.setState({ focus: onFocus });
    }
  };

  requestLivePreview = async data => {
    const { isLivePreviewOpen, refinerRules } = this.state;
    const organizationId = this.state.organizationId
      ? this.state.organizationId
      : 1;
    if (isLivePreviewOpen) {
      // Promise tracker to display a loading spinner
      trackPromise(
        SwaggerAPI.requestLivePreview(
          { data, refinerRules },
          organizationId
        ).then(result => {
          this.setState({ livePreviewSwagger: result });
        })
      );
    }
  };

  render() {
    const {
      currentNode,
      data,
      focus,
      isLivePreviewOpen,
      isEditMenuOpen,
      livePreviewSwagger
    } = this.state;
    const { classes, location } = this.props;
    const modelId = location.state ? location.state.modelId : null;

    const preview = (
      <ResizePanel
        direction="w"
        handleClass={classes.customHandle}
        borderClass={classes.customResizeBorder}
        style={{
          position: 'inherit',
          right: '0',
          width: '45%',
          maxWidth: '80%',
          zIndex: 10
        }}
      >
        <LivePreview
          swagger={livePreviewSwagger}
          onPreviewClick={this.onPreviewClick}
        />
      </ResizePanel>
    );

    let treeWidth = '';

    if (isLivePreviewOpen) {
      treeWidth = isEditMenuOpen ? 'small-tree' : 'left-half-tree';
    } else {
      treeWidth = isEditMenuOpen ? 'right-half-tree' : 'full-tree';
    }

    return data ? (
      <div className={classes.svgContainer}>
        <Slide direction="right" in={isEditMenuOpen} mountOnEnter unmountOnExit>
          <EditNodeMenu
            organization={this.state.organizationId}
            modelId={this.state.modelId}
            node={currentNode}
            data={data}
            onToolsMenuChange={this.onToolsMenuChange}
            onEnterDown={() => this.setFocus('tree')}
            isFocus={focus === 'menu'}
            onEditClick={this.onEditClick}
          />
        </Slide>
        <div className={`relative-container ${treeWidth}`}>
          {this.context.user.organizations ? (
            <>
              <Tree
                {...this.props}
                animated
                data={data}
                modelId={modelId}
                currentNode={currentNode}
                isFocus={focus === 'tree'}
                width={treeWidth}
                onNodeSelect={this.onNodeSelect}
                onToolsMenuChange={this.onToolsMenuChange}
                onTreeUpdate={this.onTreeUpdate}
                newModel={() => this.handleNewModelWarningOpen()}
                onLetterDown={() => this.setFocus('menu')}
                onFocus={() => this.setFocus('tree')}
                onUndo={this.onUndo}
              />
            </>
          ) : (
            <>
              <Tree
                {...this.props}
                animated
                data={data}
                modelId={modelId}
                currentNode={currentNode}
                isFocus={focus === 'tree'}
                width={treeWidth}
                onNodeSelect={this.onNodeSelect}
                onToolsMenuChange={this.onToolsMenuChange}
                onTreeUpdate={this.onTreeUpdate}
                newModel={() => this.handleNewModelWarningOpen()}
                onLetterDown={() => this.setFocus('menu')}
                onFocus={() => this.setFocus('tree')}
                onUndo={this.onUndo}
              />
            </>
          )}
        </div>
        <Slide
          direction="left"
          in={isLivePreviewOpen}
          mountOnEnter
          unmountOnExit
        >
          {preview}
        </Slide>
        {!isLivePreviewOpen && (
          <IconButton
            id="previewShowButton"
            color="primary"
            onClick={() => this.onPreviewClick()}
            className={classes.showIcon}
          >
            <VisibilityIcon />
          </IconButton>
        )}
        {!isEditMenuOpen && (
          <IconButton
            id="editShowButton"
            color="primary"
            onClick={() => this.onEditClick()}
            className={classes.showIconEdit}
          >
            <VisibilityIcon />
          </IconButton>
        )}
      </div>
    ) : null;
  }
}

export default withRouter(withStyles(styles)(MindmapView));
