import React, { PureComponent } from 'react';
import SwaggerUI from 'swagger-ui-react';
import { withStyles } from '@material-ui/core/styles';
import 'swagger-ui-react/swagger-ui.css';
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';
import IconButton from '@material-ui/core/IconButton';
import Spinner from './LoaderIndicator';
import styles from '../../styles/LivePreview.css';

class LivePreview extends PureComponent {
  render() {
    const { classes, swagger, id, onPreviewClick } = this.props;
    return (
      <div id={id} className="right-tree">
        {swagger && (
          <>
            <Spinner className={classes.spinner} />
            <IconButton
              id="previewHideButton"
              onClick={onPreviewClick}
              className={classes.hideIcon}
            >
              <VisibilityOffIcon />
            </IconButton>

            <SwaggerUI
              spec={swagger}
              docExpansion="list"
              showExtensions="true"
            />
          </>
        )}
      </div>
    );
  }
}

export default withStyles(styles)(LivePreview);
