/* eslint-disable jsx-a11y/label-has-associated-control */
/* eslint-disable jsx-a11y/label-has-for */
import React, { Component } from 'react';
import {
  Tooltip,
  TextField,
  FormControl,
  InputLabel,
  Select,
  OutlinedInput,
  Checkbox,
  ExpansionPanel,
  ExpansionPanelSummary,
  Typography,
  ExpansionPanelDetails,
  FormLabel,
  FormGroup,
  Paper,
  IconButton,
} from '@material-ui/core';
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { withStyles } from '@material-ui/core/styles';
import { TwitterPicker } from 'react-color';
import CustomizedCheckbox from './Checkbox';
import ModelAPI from '../../api/ModelAPI';
import styles from '../../styles/EditNodeMenu.css';
import {
  hasOperationAncestor,
  hasAncestorWithPost,
} from '../../helpers/tree_helper';

class ToolsMenu extends Component {
  state = {
    models: [],
  };

  componentDidMount() {
    ModelAPI.getModels({
      organization: this.props.organization,
      pageSize: 0,
    }).then(models => {
      this.setState({
        models: models.data.data.filter(
          model => model.id !== this.props.modelId
        ),
      });
    });
  }

  componentDidUpdate(prevProps) {
    if (this.props.isFocus !== prevProps.isFocus && this.nameInput) {
      this.nameInput.focus();
    }
  }

  onCheckBoxChange = ({ target }) => {
    const node = { [target.name]: target.checked };
    this.props.onToolsMenuChange(node);
  };

  onInversedCheckBoxChange = ({ target }) => {
    const node = { [target.name]: !target.checked };
    this.props.onToolsMenuChange(node);
  };

  onVerbButtonClick = ({ target }) => {
    const node = { verbs: [target.name] };
    this.props.onToolsMenuChange(node);
  };

  onNodeTypeChange = ({ target }) => {
    const node = { [target.name]: target.value };
    if (target.value === 'reference') {
      node.referenceId = this.state.models[0].id;
      node.children = [];
    }
    this.props.onToolsMenuChange(node);
  };

  onTextFieldChange = ({ target }) => {
    const node = { [target.name]: target.value };
    this.props.onToolsMenuChange(node);
  };

  onDropDownChange = ({ target }) => {
    const node = { [target.name]: target.value };
    this.props.onToolsMenuChange(node);
  };

  onBranchColorChange = color => {
    const node = { branchColor: color.hex };
    this.props.onToolsMenuChange(node);
  };

  onSubpathCheckboxChange = ({ target }) => {
    const node = { isSubPath: target.checked };
    // onToolsMenuChange requires to set the verbs in order to autoselect operations
    // when creating a new child node
    // Also, we can't just pass READ and UPDATE, all fields are required
    if (target.checked) {
      node.verbs = ['SEARCH', 'POST', 'PUT', 'GET', 'DELETE'];
    }
    this.props.onToolsMenuChange(node);
  };

  handleChange = name => event => {
    this.setState({ ...name, [name]: event.target.checked });
  };

  render() {
    const { node, classes, data, onEditClick } = this.props;
    node.patchable = !!node.patchable;
    node.notImplemented = !!node.notImplemented;
    node.xDescription = node.xDescription || '';
    node.verbs = node.verbs || ['SEARCH', 'POST', 'PUT', 'GET', 'DELETE'];
    node.branchColor = node.branchColor || '#FCB900';

    node.verbs = node.verbs.filter(verb => verb !== 'UPDATE'); // Update should not be a verb
    const nodeHasOperationAncestor = hasOperationAncestor(node, data);
    const nodeHasAncestorWithPost = hasAncestorWithPost(node, data);

    return (
      <div className={classes.editContainer}>
        <div className={classes.fieldContainer}>
          <div className={classes.hideIconContainer}>
            <IconButton
              id="editHideButton"
              onClick={onEditClick}
              className={classes.hideEditIcon}
            >
              <VisibilityOffIcon />
            </IconButton>
          </div>
          <label className="title-menu">Edit attribute</label>

          <Tooltip
            title="Name of the attribute. Special case: if you specify comma-separated words, they'll serve as a list of possible values for the parent"
            placement="right"
          >
            <TextField
              disabled={!node.parent}
              InputLabelProps={{ shrink: true }}
              id="outlined-email-input"
              label="Label"
              name="name"
              margin="normal"
              variant="outlined"
              onChange={this.onTextFieldChange}
              onKeyDown={e => {
                if (e.keyCode === 13) {
                  this.props.onEnterDown();
                }
              }}
              className="item-sidepanel"
              value={node.name}
              inputRef={input => {
                this.nameInput = input;
              }}
            />
          </Tooltip>

          {node.parent && (
            <>
              <Tooltip
                title="A description to help developer who will use the API"
                placement="right"
              >
                <TextField
                  multiline
                  disabled={!node.parent}
                  InputLabelProps={{ shrink: true }}
                  id="outlined-textarea"
                  label="Description"
                  name="description"
                  margin="normal"
                  variant="outlined"
                  onChange={this.onTextFieldChange}
                  className="item-sidepanel"
                  value={node.description}
                />
              </Tooltip>
              <Tooltip
                title="The description that isn't public or included in the specification but only displayed to help API"
                placement="right"
              >
                <TextField
                  multiline
                  disabled={!node.parent}
                  InputLabelProps={{ shrink: true }}
                  id="outlined-email-input"
                  label="Example"
                  type="example"
                  name="example"
                  autoComplete="example"
                  margin="normal"
                  variant="outlined"
                  onChange={this.onTextFieldChange}
                  className="item-sidepanel"
                  value={node.example}
                />
              </Tooltip>
              <FormControl
                disabled={!node.parent}
                variant="outlined"
                margin="normal"
              >
                <InputLabel
                  ref={ref => {
                    this.InputLabelRef = ref;
                  }}
                  htmlFor="type-select"
                >
                  Type
                </InputLabel>
                <Select
                  className="dropdown"
                  native
                  value={node.type}
                  onChange={this.onNodeTypeChange}
                  input={
                    <OutlinedInput
                      name="type"
                      labelWidth={35}
                      id="type-select"
                    />
                  }
                >
                  <option value="integer">integer</option>
                  <option value="number">number</option>
                  <option value="string">text</option>
                  <option value="boolean">boolean</option>
                  <option value="array">list</option>
                  <option value="object">object</option>
                  <option value="operation">operation</option>
                  <option value="reference">reference to...</option>
                </Select>
              </FormControl>
              {node.type === 'reference' && (
                <Paper className={classes.referencePaper}>
                  <Typography>Reference</Typography>
                  <FormControl variant="outlined" margin="normal">
                    <InputLabel
                      ref={ref => {
                        this.InputLabelRef = ref;
                      }}
                      htmlFor="reference-select"
                    >
                      Model
                    </InputLabel>
                    <Select
                      className="dropdown"
                      native
                      value={node.referenceId}
                      onChange={this.onDropDownChange}
                      input={
                        <OutlinedInput
                          name="referenceId"
                          labelWidth={45}
                          id="reference-select"
                        />
                      }
                    >
                      {this.state.models.map(model => {
                        return (
                          <option key={model.id} value={model.id}>{`${
                            model.name
                          }${
                            model.freezeTag !== '0' ? ' (frozen)' : ''
                          }`}</option>
                        );
                      })}
                    </Select>
                  </FormControl>
                  <Tooltip
                    title="Determine if the reference model should be a simple object or an array"
                    placement="right"
                  >
                    <label>
                      <Checkbox
                        color="default"
                        onChange={this.onCheckBoxChange}
                        type="checkbox"
                        name="isArray"
                        checked={node.isArray}
                      />
                      Is Array
                    </label>
                  </Tooltip>
                  <>
                    <Tooltip
                      title="Define branch color (with children)"
                      placement="right"
                    >
                      <label
                        style={{ fontSize: '14px', 'padding-bottom': '10px' }}
                      >
                        Color Branch
                      </label>
                    </Tooltip>
                    <TwitterPicker
                      color={node.branchColor}
                      triangle="hide"
                      width="auto"
                      onChangeComplete={this.onBranchColorChange}
                      style={{ 'padding-bottom': '20px', margin: 'auto' }}
                    />
                  </>
                </Paper>
              )}
              {node.type !== 'reference' && node.type !== 'operation' && !nodeHasOperationAncestor && (
                <Tooltip
                  title="make this field searchable (include it in query params for searchs with GET /objects?field=...)"
                  placement="right"
                >
                  <label>
                    <Checkbox
                      disabled={!node.parent}
                      color="default"
                      onChange={this.onCheckBoxChange}
                      type="checkbox"
                      name="queryable"
                      checked={node.queryable}
                    />
                    Searchable
                  </label>
                </Tooltip>
              )}
              {node.type !== 'operation' && !nodeHasOperationAncestor && (
                <Tooltip
                  title="make this field appear in search results (reponse to GET /objects)"
                  placement="right"
                >
                  <label>
                    <Checkbox
                      disabled={!node.parent}
                      color="default"
                      onChange={this.onCheckBoxChange}
                      type="checkbox"
                      name="abstract"
                      checked={node.abstract}
                    />
                    In abstract
                  </label>
                </Tooltip>
              )}
            </>
          )}
          {node.type !== 'reference' && (
            <ExpansionPanel>
              <ExpansionPanelSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
                <Typography className={classes.heading}>
                  Advanced settings
                </Typography>
              </ExpansionPanelSummary>
              <ExpansionPanelDetails className={classes.ExpansionPanelDetails}>
                <>
                  <Tooltip
                    title="Define color branch (with children)"
                    placement="right"
                  >
                    <label
                      style={{ fontSize: '14px', 'padding-bottom': '10px' }}
                    >
                      Color branch
                    </label>
                  </Tooltip>
                  <TwitterPicker
                    color={node.branchColor}
                    triangle="hide"
                    width="auto"
                    onChangeComplete={this.onBranchColorChange}
                    style={{ 'padding-bottom': '20px', margin: 'auto' }}
                  />
                </>
                {node.parent && !nodeHasOperationAncestor && (
                  <>
                    <Tooltip
                      title="Make a sub context for this field (will be accessible by GET /objects/thisfield)."
                      placement="right"
                    >
                      <label>
                        <Checkbox
                          disabled={!node.parent}
                          color="default"
                          onChange={this.onSubpathCheckboxChange}
                          type="checkbox"
                          name="isSubPath"
                          checked={node.isSubPath}
                        />
                        Subpath
                      </label>
                    </Tooltip>
                  </>
                )}
                {node.parent && !nodeHasOperationAncestor && !node.isSubPath && (
                  <>
                    <Tooltip
                      title="When this box is checked, the attribute will be individually updatable. Otherwise, updates are only possible for all the fields together."
                      placement="right"
                    >
                      <label>
                        <Checkbox
                          disabled={!node.parent}
                          color="default"
                          onChange={this.onCheckBoxChange}
                          type="checkbox"
                          name="patchable"
                          checked={node.patchable}
                        />
                        Partial update
                      </label>
                    </Tooltip>
                  </>
                )}
                {node.parent && (
                  <>
                    <Tooltip
                      title="Fields that are not yet implemented do not appear in the specification result"
                      placement="right"
                    >
                      <label style={{ fontSize: '14px' }}>
                        <Checkbox
                          disabled={!node.parent}
                          color="default"
                          onChange={this.onCheckBoxChange}
                          type="checkbox"
                          name="notImplemented"
                          checked={node.notImplemented}
                        />
                        Not yet implemented
                      </label>
                    </Tooltip>
                  </>
                )}
                {node.parent && node.type !== 'operation' && !node.isSubPath && (
                  <>
                    <Tooltip
                      title="This field will be present on request payload (POST body) at creation time if checked"
                      placement="right"
                    >
                      <label>
                        <Checkbox
                          disabled={!node.parent}
                          color="default"
                          onChange={this.onInversedCheckBoxChange}
                          type="checkbox"
                          name="calculated"
                          checked={!node.calculated}
                        />
                        {nodeHasOperationAncestor
                          ? 'In Operation Request'
                          : 'Known on create'}
                      </label>
                    </Tooltip>
                  </>
                )}
                {node.type !== 'operation' &&
                  !node.isSubPath &&
                  node.parent &&
                  (nodeHasOperationAncestor || nodeHasAncestorWithPost) && (
                    <Tooltip
                      title="make the field appear in POST responses"
                      placement="right"
                    >
                      <label>
                        <Checkbox
                          color="default"
                          onChange={this.onCheckBoxChange}
                          type="checkbox"
                          name="isInPostResponse"
                          checked={node.isInPostResponse}
                        />
                        {nodeHasOperationAncestor
                          ? 'In Operation Response'
                          : 'In Create Response'}
                      </label>
                    </Tooltip>
                  )}
                {node.type !== 'reference' && !nodeHasOperationAncestor && (
                  <>
                    <Tooltip
                      title="make the results paginated (adds the query parameters page & pageSize)"
                      placement="right"
                    >
                      <label>
                        <Checkbox
                          disabled={
                            ((!node.isSubPath || node.type !== 'array') &&
                              !!node.parent) ||
                            false
                          }
                          color="default"
                          onChange={this.onCheckBoxChange}
                          type="checkbox"
                          name="paginable"
                          checked={node.paginable}
                        />
                        Pagination
                      </label>
                    </Tooltip>

                    <Tooltip
                      title="Optionally define here additionals query params, comma-separated. Those terms come in addition to the ones that are 'searchable'"
                      placement="right"
                    >
                      <TextField
                        disabled={
                          ((!node.isSubPath || node.type !== 'array') &&
                            !!node.parent) ||
                          false
                        }
                        label="Query Params"
                        type="queryParams"
                        name="queryParams"
                        margin="normal"
                        variant="outlined"
                        onChange={this.onTextFieldChange}
                        className="item-sidepanel"
                        value={node.queryParams || ''}
                      />
                    </Tooltip>
                    <Tooltip
                      title="An optional, internal explanation for those who have to implement this API. For instance the database where to find the data for this attribute. (Not included in the public specification)"
                      placement="right"
                    >
                      <TextField
                        multiline
                        disabled={!node.parent}
                        InputLabelProps={{ shrink: true }}
                        id="outlined-textarea"
                        label="Internal Description"
                        name="xDescription"
                        margin="normal"
                        variant="outlined"
                        onChange={this.onTextFieldChange}
                        className="item-sidepanel"
                        value={node.xDescription}
                      />
                    </Tooltip>
                  </>
                )}
                {(node.isSubPath || !node.parent) && (
                  <FormControl
                    component="fieldset"
                    className={classes.formControl}
                  >
                    <FormLabel className={classes.formLabel} component="legend">
                      Operations
                    </FormLabel>
                    <FormGroup>
                      {(!node.parent || node.type === 'array') && (
                        <CustomizedCheckbox
                          customcolor="#61affe"
                          label="SEARCH"
                          name="SEARCH"
                          type="checkbox"
                          checked={node.verbs.includes('SEARCH')}
                          onChange={this.onVerbButtonClick}
                        />
                      )}
                      {(!node.parent || node.type === 'array') && (
                        <CustomizedCheckbox
                          customcolor="#49cc90"
                          label="CREATE"
                          name="POST"
                          type="checkbox"
                          checked={node.verbs.includes('POST')}
                          onChange={this.onVerbButtonClick}
                        />
                      )}
                      <CustomizedCheckbox
                        customcolor="#61affe"
                        label="READ"
                        name="GET"
                        type="checkbox"
                        checked={node.verbs.includes('GET')}
                        onChange={this.onVerbButtonClick}
                      />
                      <CustomizedCheckbox
                        customcolor="#fca130"
                        label="UPDATE"
                        name="PUT"
                        type="checkbox"
                        checked={node.verbs.includes('PUT')}
                        onChange={this.onVerbButtonClick}
                      />
                      {(!node.parent || node.type === 'array') && (
                        <CustomizedCheckbox
                          customcolor="#f93e3e"
                          label="DELETE"
                          name="DELETE"
                          type="checkbox"
                          checked={node.verbs.includes('DELETE')}
                          onChange={this.onVerbButtonClick}
                        />
                      )}
                    </FormGroup>
                  </FormControl>
                )}
              </ExpansionPanelDetails>
            </ExpansionPanel>
          )}
        </div>
      </div>
    );
  }
}

export default withStyles(styles)(ToolsMenu);
