/* eslint-disable react/sort-comp */
/* eslint-disable camelcase */
/* eslint-disable jsx-a11y/no-noninteractive-tabindex */
/* eslint-disable no-shadow */
/* eslint-disable react/forbid-prop-types */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/anchor-is-valid */
import PropTypes from 'prop-types';
import React, { Component } from 'react';
import keydown from 'react-keydown';
import Link from './Link';
import Node from './Node';
import ContextMenu from './ContextMenu';

const propTypes = {
  keyProp: PropTypes.string.isRequired,
  labelProp: PropTypes.string.isRequired,
  gProps: PropTypes.object.isRequired,
  textProps: PropTypes.object.isRequired
};

class Mindmap extends Component {
  constructor(props) {
    super(props);

    this.state = {
      panX: 0,
      panY: 0,
      zoom: 0.7,
      isPaning: false,
      isDragging: false,
      isDraggingMoving: false,
      showContext: false,
      contextContainerStyle: {}
    };

    this.nodeContext = false;
  }

  UNSAFE_componentWillReceiveProps({ keydown }) {
    if (keydown.event) {
      this.props.onKeyPress({ keydown });
    }
  }

  componentDidUpdate(prevProps) {
    if (
      prevProps.isFocus !== this.props.isFocus &&
      this.props.isFocus &&
      this.mindmap
    ) {
      this.mindmap.focus();
    }
  }

  openNodeContextMenu = e => {
    e.preventDefault();
    this.nodeContext = true;
    this.setState({
      showContext: true,
      contextContainerStyle: {
        display: 'block',
        top: `${e.clientY}px`,
        left: `${e.clientX}px`
      }
    });
  };

  closeContextMenu = () => {
    this.setState({
      showContext: false
    });
  };

  onWheelScroll = e => {
    let zoom = this.state.zoom;
    if (e.deltaY < 0) {
      zoom *= 1.1;
      if (zoom > 3) zoom = 3;
    } else {
      zoom *= 0.94;
      if (zoom < 0.4) zoom = 0.4;
    }
    this.setState({ zoom });
  };

  onDragStart = (nodeName, nodeKey) => {
    this.setState({
      isPaning: false,
      isDragging: true,
      isDraggingMoving: false,
      draggedNodeKey: nodeKey
    });
  };

  onDragStop = (nodeKey, direction) => {
    this.setState({
      isPaning: false,
      isDragging: false,
      isDraggingMoving: false
    });
    this.props.moveNode(this.state.draggedNodeKey, nodeKey, direction);
  };

  onPanStart = e => {
    const startX =
      typeof e.clientX === 'undefined'
        ? e.changedTouches[0].clientX
        : e.clientX;
    const startY =
      typeof e.clientY === 'undefined'
        ? e.changedTouches[0].clientY
        : e.clientY;
    this.setState({ isPaning: true, isDragging: false, startX, startY });
  };

  onPanMove = e => {
    if (this.state.isDragging) {
      this.setState({ isDraggingMoving: true });
      return;
    }

    if (!this.state.isPaning) {
      return;
    }
    const x =
      typeof e.clientX === 'undefined'
        ? e.changedTouches[0].clientX
        : e.clientX;
    const y =
      typeof e.clientY === 'undefined'
        ? e.changedTouches[0].clientY
        : e.clientY;

    this.setState({
      panX: this.state.panX + x - this.state.startX,
      panY: this.state.panY + y - this.state.startY,
      startX: x,
      startY: y
    });
  };

  onPanEnd = () => {
    this.setState({
      isPaning: false,
      isDragging: false,
      isDraggingMoving: false
    });
  };

  render() {
    return (
      <div
        tabIndex="0"
        id="mindmap"
        onClick={this.closeContextMenu}
        onFocus={() => this.props.onFocus()}
        ref={ref => {
          this.mindmap = ref;
        }}
      >
        <div
          className="custom-menu"
          id="context-container"
          style={this.state.contextContainerStyle}
        >
          {this.state.showContext && <ContextMenu {...this.props} />}
        </div>
        <svg
          viewBox={`${this.props.minX} ${this.props.minY} ${this.props.maxX -
            this.props.minX +
            this.props.nodeWidth +
            1} ${this.props.maxY - this.props.minY + 1}`}
          className="custom"
          onMouseDown={this.onPanStart}
          onMouseMove={this.onPanMove}
          onMouseLeave={this.onPanEnd}
          onMouseUp={this.onPanEnd}
          onWheel={this.onWheelScroll}
          style={{ pointerEvents: 'auto', height: '100%' }}
        >
          <g
            transform={` translate(${this.state.panX} ${this.state.panY}) scale(${this.state.zoom}) `}
          >
            {this.props.links.map(link => (
              <Link
                colorScheme={link.target.data.name.charCodeAt(0)}
                branchColor={link.target.data.branchColor}
                isDottedLine={link.target.data.notImplemented}
                key={link.target.data[this.props.keyProp]}
                keyProp={this.props.keyProp}
                source={link.source}
                target={link.target}
                x1={link.source.x}
                x2={link.target.x}
                y1={
                  link.source.y +
                  (link.source.data.direction === 'right' &&
                  link.target.data.direction === 'right'
                    ? link.source.data.name.length * 9
                    : 0) -
                  (link.source.data.direction === 'left' &&
                  link.target.data.direction === 'left'
                    ? link.source.data.name.length * 9
                    : 0)
                }
                y2={
                  link.target
                    .y /* + ((link.target.data.direction === "left"	)?link.target.data.name.length*9:0)  */
                }
                pathProps={{
                  ...this.props.pathProps,
                  ...link.target.data.pathProps
                }}
              />
            ))}
            {this.props.nodes
              .filter(n => n.data.nodeKey)
              .map(node => (
                <g key={node.data.nodeKey}>
                  <Link
                    branchColor={node.data.branchColor}
                    isDottedLine={node.data.notImplemented}
                    x1={node.x}
                    x2={node.x}
                    y1={
                      node.y +
                      (node.data.direction === 'right'
                        ? node.data.name.length * 9
                        : 0)
                    }
                    y2={
                      node.y -
                      (node.data.direction === 'left'
                        ? node.data.name.length * 9
                        : 0)
                    }
                    pathProps={{
                      ...this.props.pathProps,
                      ...node.data.pathProps
                    }}
                  />
                  <Node
                    node={node}
                    currentNode={this.props.currentNode}
                    onNodeSelect={this.props.onNodeSelect}
                    addNewChildNode={this.props.addNewChildNode}
                    onDragStart={this.onDragStart}
                    onDragStop={this.onDragStop}
                    openContextMenu={this.openNodeContextMenu}
                    isDraggingMoving={this.state.isDraggingMoving}
                    key={node.data[this.props.keyProp]}
                    keyProp={this.props.keyProp}
                    labelProp={this.props.labelProp}
                    x={node.x}
                    y={node.y}
                    Width={this.props.nodeWidth}
                    gProps={{
                      ...this.props.gProps,
                      ...node.data.gProps
                    }}
                    textProps={{
                      ...this.props.textProps,
                      ...node.data.textProps
                    }}
                    {...node.data}
                  />
                </g>
              ))}
          </g>
        </svg>
      </div>
    );
  }
}

Mindmap.propTypes = propTypes;
export default keydown(Mindmap);
