/* eslint-disable react/forbid-prop-types */
/* eslint-disable no-bitwise */
import PropTypes from 'prop-types';
import React, { PureComponent } from 'react';

const propTypes = {
  x1: PropTypes.number.isRequired,
  x2: PropTypes.number.isRequired,
  y1: PropTypes.number.isRequired,
  y2: PropTypes.number.isRequired,
  pathProps: PropTypes.object.isRequired
};

function diagonal(x1, y1, x2, y2) {
  return `M${y1},${x1}C${(y1 + y2) / 2},${x1} ${(y1 + y2) /
    2},${x2} ${y2},${x2}`;
}

export default class Link extends PureComponent {
  render() {
    const d = diagonal(
      this.props.x1,
      this.props.y1,
      this.props.x2,
      this.props.y2
    );

    const colorScheme = this.props.colorScheme & 31;
    const red = 245 + colorScheme / 3;
    const green = 166 + colorScheme;
    const blue = 35 + colorScheme;
    const color = this.props.branchColor || `rgb(${red}, ${green}, ${blue})`;
    return (
      <path
        style={{ stroke: color }}
        {...this.props.pathProps}
        {...(this.props.isDottedLine && { 'stroke-dasharray': '5, 5' })}
        d={d}
      />
    );
  }
}

Link.propTypes = propTypes;
